.app-wraper {
    display: grid;
    grid-gap: 10px;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 25% 74%;
    grid-template-areas:
  "h h"
  "n c";
}

.app-wraper-content {
    grid-area: c;
    background-color: rgb(0, 183, 255);
    position: relative;
    height: calc(100vh - 90px);
    overflow: auto;
}




